.section-search__wrapper{
  padding:200px 0 400px 0;
}

.search {
  &__wrapper {
    max-width: 1260px;
    width: 100%;

    margin: 0 auto;

    min-height: 665px;
  }

  &__title {
    text-align: center;

    margin: 0 auto;

    max-width: 1200px;

    font-size: 125px;
    line-height: 90%;
  }

  &__content {
    margin: 60px 0 0 0;
  }

  &__input {
    position: relative;
    height: 60px;

    &::after {
      content: '';
      position: absolute;
      top: calc(50% - 11px);
      right: 22px;
      width: 24px;
      height: 22px;

      background: url("../img/search-arrow.svg") center center/contain no-repeat;
    }

    &-item {
      width: 100%;
      height: 100%;
      padding: 22px 55px 22px 20px;

      background: #373737;
      border-radius: 10px;

      font-family: 'Inter', sans-serif;
      font-weight: 400;
      font-size: 16px;
      line-height: 19px;

      color: #E3EBF6;

      &::placeholder {
        color: rgba(227, 235, 246, 0.34);
        opacity: 1;
      }
    }
  }

  &__box{
    margin: 50px 0 0 0;
  }

  &__result {

    background: #1E1E1E;
    border-radius: 10px;
    overflow: hidden;

    &-mobile {
      display: none;
    }
  }

  &__footer {
    margin: 50px 0 0 0;
    display: none;
    align-items: center;
    justify-content: center;
    &.active{
      display: flex;
    }
  }

  &-result {
    &-desktop,&-mobile{
      display: none;
      &.active{
        display: block;
      }
    }
    &__block {
      & + & {
        position: relative;

        &::before {
          content: '';
          position: absolute;
          left: -5px;
          top: calc(50% - 20px);
          width: 1px;
          height: 40px;

          background: #303030;
        }
      }
    }

    &__row {
      display: grid;
      grid-template-columns: 180px 100px auto 150px 150px 150px;
      grid-auto-rows: 104px;
      gap: 8px;


      &:nth-child(odd) {
        background: #303030;

        .search-result__block + .search-result__block {
          &::before {
            background: #161616;
          }
        }
      }

      &:nth-child(odd){
        &.search-result__row-header {
          background: #1E1E1E;
          .search-result__block + .search-result__block {
            &::before {
              background: #303030;

              height: 40px;
              top: calc(50% - 20px);
            }
          }
        }
      }

      &_size {
        &-small {
          grid-auto-rows: 50px;

          .search-result__block + .search-result__block {
            &::before {
              height: 40px;
              top: calc(50% - 20px);
            }
          }
        }

        &-full {
          grid-template-columns: auto;

          padding: 0 10px;
        }
      }

      .search-result__block + .search-result__block {
        &::before {
          height: 84px;
          top: calc(50% - 42px);
        }
      }
    }

    &__text {
      font-family: 'Lato', sans-serif;
      font-style: normal;
      font-weight: 400;
      font-size: 16px;
      line-height: 165%;

      color: #E3E3E3;

      width: 100%;

      &_position {
        &-center {
          text-align: center;
        }
      }
    }

    &__link {
      text-decoration-line: underline;
      color: #44D4AC;
    }

    &__img {
      display: flex;
      align-items: center;
      justify-content: center;
      width: 100%;

      &-item {
        width: 40px;
        flex-shrink: 0;
        height: 60px;
        background: #373737;
        border-radius: 5px;

        border: none;

        object-fit: cover;
      }
    }

    &__block {
      padding: 12px 10px;

      display: flex;
      align-items: center;
    }

    &-content{
      max-height: 570px;
      overflow-x: hidden;
      overflow-y: auto;
    }
  }

  &-slider {
    .swiper-wrapper {
      justify-content: stretch;
    }

    .swiper-slide {
      height: auto;
    }

    &-scrollbar {
      margin: 23px 0 0 0;
    }
  }
}

@media screen and (max-width: 1200px) {
  .section{
    &-search{
      &__wrapper{
        padding:40px 0;
        min-height: 799px;

        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;

        width: 100%;
      }
    }
  }
  .search {
    &__wrapper {
      max-width: calc(100vw - 32px);
      min-height: auto;
    }

    &__content {
      margin: 20px 0 0 0;
    }

    &__title {
      font-size: 45px;
      max-width: 330px;

      &-desktop {
        display: none;
      }
    }
    &__box{
      margin: 10px 0 0 0;
    }
    &__result {
      &-desktop {
        display: none;
      }

      &-mobile {
        display: block;
      }
    }

    &-result {
      height: 100%;

      &__wrapper {
        height: 100%;
        background: #303030;
        border-width: 1px 0px;
        border-style: solid;
        border-color: #161616;
        border-radius: 10px;

        padding: 10px;
      }

      &__block{
        padding: 0;
        & + & {
          margin: 6px 0 0 0;
        }
      }

      &__img {
        &-item {
          width: 100%;
          height: 308px;
          border-radius: 10px;
        }
      }

      &__content {
        margin: 20px 0 0 0;
      }

      &__text {
        font-size: 14px;
        line-height: 130%;

        display: flex;
        align-items: center;
        flex-wrap: wrap;

        span {
          margin: 0 3px;
        }
      }

      &__title {
        color: #8D8D8D;
      }
    }
  }
}