.advantages {
  &__grid {
    display: grid;
    grid-template-columns: repeat(4, minmax(0, 1fr)) 320px;

    &-item {
      border-left: 1px solid #B6B6B6;

      &:last-child{
        .advantages{
          &-item{
            &__wrapper{
              padding-right: 0;
            }
          }
        }
      }
    }
  }

  &-item {
    &__wrapper {
      padding: 20px 27px 50px 27px;
    }

    &__header + &__content {
      margin: 60px 0 0 0;
    }

    &__title{
      min-height: 64px;
    }

    &__content {
      &_small {
        max-width: 280px;
      }
      &_medium{
        max-width: 287px;
      }
      &_big {
        max-width: 300px;
      }
    }

    &__number {
      font-family: 'Bebas Neue Cyrillic', sans-serif;
      font-weight: 400;
      font-size: 176px;
      line-height: 90%;

      color: transparent;

      letter-spacing: -0.05em;

      -webkit-text-stroke-width: 3px;
      -webkit-text-stroke-color: #FFFFFF;
    }

    &__title {
      font-family: 'Bebas Neue Cyrillic', sans-serif;
      font-weight: 400;
      font-size: 36px;
      line-height: 90%;

      color: #FFFFFF;
    }

    &__text {
      font-family: 'Inter', sans-serif;
      font-weight: 400;
      font-size: 16px;
      line-height: 140%;

      color: #FFFFFF;

      &_bold {
        font-weight: 600;
        font-size: 18px;
      }

      & + & {
        margin: 15px 0 0 0;
      }
    }
  }
}

.section {
  &-advantages {
    &__footer {
      display: none;
      margin: 40px 0 0 0;
    }
  }
}


@media screen and (max-width: 1700px) {
  .advantages {
    &__grid{
      grid-template-columns: repeat(5,minmax(0,1fr));
    }
    &-item {
      &__wrapper {
        padding: 20px 20px 30px 40px;
      }
    }
  }
}

@media screen and (max-width: 1366px) {
  .advantages {
    &-item {
      &__header + &__content {
        margin: 50px 0 0 0;
      }
    }
  }
}

@media screen and (max-width: 1100px) {
  .section {
    &-advantages {
      &__footer {
        display: block;
      }
    }
  }
  .advantages {
    &__grid {
      min-width: 1500px;
    }
  }
}

@media screen and (max-width: 600px) {
  .advantages {
    &__grid {
      grid-template-columns: repeat(5, minmax(0, calc(100vw - 32px)));
      min-width: auto;
      width: max-content;

      &-item {
        border: none;
      }
    }

    &-item {

      &__number {
        font-size: 100px;
        line-height: 90%;
      }

      &__title {

        font-size: 36px;
        line-height: 90%;
      }

      &__wrapper {
        padding: 5px 0 0 0;
      }
    }
  }
}